@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Poppins:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Poppins:wght@400;700&display=swap);


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav#navbar--dom{
    background-color: #082032;
    padding: 46px 0px 0px 0px;
}
.container-fluid{
    padding: 0 !important;
    align-items: baseline !important;

}
button.navbar-toggler{
    margin-right: 32px;
}
.icon-menu{
    color: #F0F8FE;
    font-size: 32px;
    
}
span.navbar-toggler-icon{
    color: #F0F8FE;

}
.navbar--title{
    margin: 0 0 22px 32px;
    color: #F0F8FE;
    -webkit-text-decoration: underline #6FBBF4;
            text-decoration: underline #6FBBF4;
}
.navbar--title:hover{
    color: #F0F8FE;
}
.navbar-collapse{
    background-color: #F0F8FE !important;
    padding-bottom: 24px;
}
ul h3{
    text-align: start;
    margin-bottom: 24px;
    padding: 0;
    transition: .2s;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 16px;
}
ul h3:hover{
    font-weight: 700;
    color: #082032;
}
.navlink{
    text-decoration: none;
    color: #082032;
    text-align: center;
}
li{
    margin: 15px 20px 0 30px;
    border-bottom: solid #082032 1px;
    padding: 0;
}
@media(min-width: 992px){
    .navbar-collapse{
        background-color: #082032 !important;
        flex-grow: 0;
        margin-right: 0;
    }
    ul h3{
        color: #F0F8FE;
        font-size: 20px;
    }
    ul h3:hover{
        font-weight: 700;
        color: #F0F8FE;
    }
}

.footer{
    display: flex;
    justify-content: space-evenly;
    padding-top: 30px;
    background-color: #F0F8FE;

}
.footer-text{
    color: #082032;
}




.section-nft{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #F5B72D;
    background-repeat: no-repeat;
    background-size: cover;
}
.section-nft-content{
    display: flex;
    flex-direction: column;
    align-items: center;
}
h1{
    color: #F0F8FE;
    font-family: 'Montsserrat', sans-serif;
    font-size: 24px;
    font-weight: 700;
}
p{
    font-family: 'Poppins', sans-serif;
    color: #F0F8FE;
    text-align: center;
}
h3{
    margin-bottom: 10px;
    padding: 0 25px 0 25px;
    text-align: center;
    font-family: 'Montsserrat', sans-serif;
    font-weight: 700;
}
h3.text-transition{
    color: #F0F8FE;
    margin-top: 50px;
    margin-bottom: 50px;

}
h2{
    text-align: center;
    margin: 16px 30px 24px 30px;
    font-weight: bold;
    font-size: 16px;
    font-family: 'Montsserrat', sans-serif;
}
.divider-1{
    width: 270px;
    height: 2px;
    background: linear-gradient(
        90deg,
        #F5B72D,
        #15252f

    );
}
button.saiba-mais{
    max-width: 140px;
    border-radius: 10px;
    padding: 8px 20px 8px 20px;
    margin-bottom: 40px;
    color: #F0F8FE;
    border: none;
}
.button-description{
    font-family: 'Montserrat', sans-serif;
    color: #15252f;
    margin: 0;
}
img.image-init{
    margin-bottom: 2rem;
}
.product-presentation{
    display: flex;
    flex-direction: column;
    background-color: #082032;
    padding: 40px;
}
.presentation-item{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.card-home-section{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
   
}
.card-games{
    background-color: #6FBBF4;
    
}
.card-arts{
    background-color: #F587EB;
}
.card-musics{
    background-color: #F2F556;
}
.card-text{
    color: #15252f;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}

@media(min-width: 768px){
    .section-nft{
        flex-direction: row;
        align-items: flex-end;
    }
    .section-nft-content{
        align-items: flex-end;
        padding: 0;
    }
    .divider-1,
    .saiba-mais{
        margin-right: 2rem;
    }
    .image-init{
        margin: 0 !important;
        width: 800px;
    }
    .product-presentation{
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;

    }
    .presentation-item{
        max-width: 200px;
    }
    .card-home-section{
        flex-direction: row;
        justify-content: space-evenly;

    }
}






